//-----------------------------------------------------------------
//.top
//-----------------------------------------------------------------

//.top-mv
//---------------------------------------------------------
.top-mv {
  background: #ccc;
  &__inner {
    @include smMax {
      padding: 0;
    }
  }
}

//.top-sect
//---------------------------------------------------------
.top-sect {
  &:nth-child(n + 2) {
    @include smMin {
      margin-top: 100px;
    }
    @include smMax {
      margin-top: 50px;
    }
  }
  &__ttl {
    text-align: center;
    @include smMin {
      font-size: 4rem;
      margin-bottom: 40px;
    }
    @include smMax {
      font-size: 3rem;
      margin-bottom: 30px;
    }
  }
  &__txt {
    @include smMin {
      text-align: center;
    }
  }
  &__btn {
    @include smMin {
      margin-top: 40px;
    }
    @include smMax {
      margin-top: 30px;
    }
  }
}

